'use client'

import * as React from 'react';
import TextField from '@mui/material/TextField'
import { Box, Paper, Stack, styled, Typography, useTheme } from '@mui/material';
import Grid from '@mui/material/Grid2';

import { useForm, Controller, SubmitHandler } from 'react-hook-form';
import { zodResolver } from "@hookform/resolvers/zod";
import { z } from "zod";
import { Horse, Heart, Cube, UserCheck, LineVertical } from "@phosphor-icons/react";
import { BaseButton } from '@/components/Button';
import { SignInFormData, SignInSchema } from '@/schemas/SignInSchema';
import { CONFIG } from '@/config/dotenv';


const RegisterPage = () => {
    const liffId = CONFIG.NEXT_PUBLIC_LIFF_ID
    let isLineInited = false;

    const initLiff = async () => {
        const liff = (await import('@line/liff')).default
        try {
            await liff.init({ liffId });
            isLineInited = true;
        } catch (error: unknown) {
            if (error instanceof Error) {
                console.error('liff init error', error.message);
            }
        }
    }


    const theme = useTheme();
    const u = theme.palette.background.default;

    return (
        <Grid

            container spacing={1}
            alignItems="center"
            justifyContent="center"
            display="flex"
            height="100vh"
            sx={{ backgroundColor: u }}
        >

            <Grid>
                <Box sx={{ py: 5, bgcolor: "white", px: 5, borderRadius: 4 }} >
                    <Box sx={{ py: 1.5 }} alignItems="center" justifyContent="center" textAlign="center">
                        <Typography sx={{ fontSize: '1rem' }}>ลงทะเบียน</Typography>
                    </Box>

                    <TextField
                        id="standard-basic"
                        placeholder='เลขประจำตัวผู้เสียภาษี'
                        fullWidth
                        slotProps={
                            {
                                input: {
                                    endAdornment: <UserCheck size={32} />
                                }
                            }
                        }
                    />
                    <Box sx={{ py: 1.5 }}  >
                        <BaseButton onTap={async () => {
                            await new Promise((resolve) => setTimeout(resolve, 3000));

                        }} text='ลงทะเบียน' color='primary' variant='contained' fullWidth />
                    </Box>

                </Box>
            </Grid>
        </Grid>




    )
}

export default RegisterPage
